import React from "react";
import request from "../helpers/request";
import Loader from "../components/Loader";
import {
  Icon,
  Grid,
  Input,
  Button,
  MenuItem,
  TextField,
  InputLabel,
  Typography,
  FormControl,
  InputAdornment,
  Table,
  TableHead,
  TableCell,
  TableRow,
  TableBody
} from "@material-ui/core";

const RenderItems = ({ items = [], renderItem }) => items.map(renderItem);

class AgregarSubcuentas extends React.Component {
  params = this.props.navigation.params();
  state = {
    subaccounts: [],
    projectAccounts: [],
    loading: true,
    values: {
      childAccount: "",
      budget: ""
    }
  };

  componentDidMount() {
    this.GetAccounts();
    this.GetProjectAccounts();
    this.setState({ loading: false });
  }

  async GetAccounts() {
    try {
      const code = this.params.account.code;
      const result = await request.get("/subcuentas?code=" + code);
      if (result.error) return alert(result.error);
      let subaccounts = result.data;
      this.setState({ subaccounts });
    } catch (message) {
      alert(message);
    }
  }

  async GetProjectAccounts() {
    try {
      const result = await request.get(
        `/project-accounts?projectId=${this.params.projectId}&accountId=${this.params.account.id}`
      );
      if (result.error) return alert(result.error);
      let projectAccounts = result.data;
      this.setState({ projectAccounts });
    } catch (message) {
      alert(message);
    }
  }

  handleChange = ev => {
    const { name, value } = ev.target;
    const { values } = this.state;
    values[name] = value;
    this.setState({ values });
  };

  pushChildAccount = async () => {
    try {
      const { values, subaccounts, projectAccounts } = this.state;

      // Validaciones de que este llenado los datos
      if (values.childAccount === "")
        return alert("Por favor seleccione una cuenta");
      if (values.budget === "")
        return alert("Por favor agregue un monto de presupuesto");

      // Definicion de la cuenta a agregar
      const child = subaccounts[parseInt(values.childAccount)];
      const project_account = {
        project_id: this.params.projectId,
        project_code: this.params.projectCode,
        project_name: this.params.projectName,
        account_id: this.params.account.id,
        account_code: this.params.account.code,
        account_name: this.params.account.name,
        subaccount_id: child.CIDPRODUCTO,
        subaccount_code: child.CCODIGOPRODUCTO,
        subaccount_name: child.CNOMBREPRODUCTO,
        subaccount_budget: parseFloat(values.budget)
      };

      // Se guarda en la base de datos
      const response = await request.post("/project-account-add", {
        item: project_account
      });
      project_account.id = response.data.insertId;

      // Se agrega a la lista
      projectAccounts.push(project_account);
      this.setState({
        projectAccounts,
        values: { childAccount: "", budget: "" }
      });
    } catch (error) {
      alert(error);
    }
  };

  removeAccount = async i => {
    const { projectAccounts } = this.state;
    const account = projectAccounts[i];
    const answ = window.confirm(
      "¿Deseas eliminar la partida " + account.subaccount_name + " realmente?"
    );

    if (answ) {
      const response = await request.delete(
        "/project-account-delete?id=" + account.id
      );
      if (response.error) return alert(response.message);
      projectAccounts.splice(i, 1);
      this.setState({ projectAccounts });
    }
  };

  render() {
    const { loading, subaccounts, values, projectAccounts } = this.state;
    return (
      <>
        <Loader show={loading} />
        <Grid container spacing={4}>
          <Grid item sm={12}>
            <Typography>
              Agregar cuentas a {this.params.account.name}
            </Typography>
          </Grid>
          <Grid item sm={4}>
            <TextField
              id="standard-select-currency"
              select
              fullWidth
              name="childAccount"
              label="Agregar cuenta"
              value={values.childAccount || ""}
              onChange={this.handleChange}
              helperText="Por favor selecciona una opcion"
            >
              {subaccounts.map((option, i) => (
                <MenuItem key={option.CCODIGOPRODUCTO} value={`${i}`}>
                  {option.CCODIGOPRODUCTO + "   " + option.CNOMBREPRODUCTO}
                </MenuItem>
              ))}
            </TextField>
          </Grid>
          <Grid item sm={2}>
            <FormControl fullWidth>
              <InputLabel htmlFor="standard-adornment-amount">
                Presupuesto USD
              </InputLabel>
              <Input
                id="standard-adornment-amount"
                name="budget"
                type="number"
                min="2"
                value={values.budget || ""}
                onChange={this.handleChange}
                startAdornment={
                  <InputAdornment position="start">$</InputAdornment>
                }
              />
            </FormControl>
          </Grid>
          <Grid item sm={2}>
            <Button
              variant="contained"
              color="primary"
              onClick={this.pushChildAccount}
            >
              <Icon>add</Icon>
            </Button>
          </Grid>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Codigo</TableCell>
                <TableCell>Descripcion</TableCell>
                <TableCell>Presupuesto</TableCell>
                <TableCell>Acciones</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <RenderItems
                items={projectAccounts}
                renderItem={(item, i) => (
                  <TableRow key={item.project_id}>
                    <TableCell>{item.subaccount_code}</TableCell>
                    <TableCell>{item.subaccount_name}</TableCell>
                    <TableCell>
                      $
                      {item.subaccount_budget.toLocaleString("es-Mx", {
                        maximumFractionDigits: 2
                      })}
                    </TableCell>
                    <TableCell>
                      <Button
                        onClick={() => this.removeAccount(i)}
                        variant="text"
                        color="secondary"
                      >
                        Eliminar
                      </Button>
                    </TableCell>
                  </TableRow>
                )}
              />
            </TableBody>
          </Table>
        </Grid>
      </>
    );
  }
}

export default AgregarSubcuentas;
